<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('BillingConditionHistory') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                        <div>
                            <div class="heading-border">
                                <h5 class="h5">
                                    <span>{{ $t('History') }}</span>
                                </h5>
                            </div>

                            <div class="listing mt-2">
                                <div class="row">
                                    <div class="col-12 mb-2">
                                        <div class="mr-2 d-inline-block">
                                            <span class="legend-Progress colorboxsquare"></span>
                                            {{ $t('CurrentVersion') }}
                                        </div>
                                    </div>
                                </div>

                                <div class="table-responsive">
                                    <table id="tbllistinghistorydata"
                                        class="table table-bordered dt-responsive nowrap mt-0 table_w800">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th><span id="ServiceName">{{ $t('ConditionName') }}</span></th>
                                                <th><span id="Condition">{{ $t('Condition') }}</span></th>
                                                <th><span id="CreatedBy">{{ $t('created_by_name') }}</span></th>
                                                <th><span id="CreatedDate">{{ $t('CreatedDate') }}</span></th>
                                                <th><span id="ModifiedBy">{{ $t('ModifiedBy') }}</span></th>
                                                <th><span id="ModifiedDate">{{ $t('ModifiedDate') }}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(data, index) in serviceHistory" :key="index"
                                                :class="{ 'low-bar': data.VERSION_NAME === 'CURRENT' }">
                                                <td>{{ data.SERVICE_NAME }}</td>
                                                <td>{{ data.CONDITION }}</td>
                                                <td>{{ data.CREATEDBY }}</td>
                                                <td>{{ $options.filters.formatDateTime(data.CREATEDDATE) }}</td>
                                                <td>{{ data.UPDATEDBY }}</td>
                                                <td>
                                                    <span v-if="data.MODIFIEDDATE">{{
                                                        $options.filters.formatDateTime(data.MODIFIEDDATE) }}</span>
                                                    <span v-else>--</span>
                                                </td>
                                            </tr>
                                            <tr v-if="!serviceHistory || serviceHistory.length === 0">
                                                <td colspan="6" class="text-danger text-center no-record">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from '../../services/DataService';
export default {
    props: {
        ServiceId: {
            type: Number,
            required: true
        }
    },
    components: {},
    data() {
        return {
            isLoading: false,
            noRecords: false,
            serviceHistory: []
        }
    },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vueObj = this;
            var params = `serviceId=${vueObj.ServiceId}`;
            await DataServices.GetServiceHistoryListing(params).then((response) => {
                debugger;
                vueObj.serviceHistory = [];
                if (response.data) {
                    vueObj.serviceHistory = response.data;
                    if (vueObj.serviceHistory.length > 0) {
                        vueObj.noRecords = false;
                    } else {
                        vueObj.noRecords = true;
                    }
                } else {
                    vueObj.noRecords = true;
                    vueObj.TotalRecords = 0
                }
            })
        },
        Close: function () {
            this.$parent.IsHistory = false;
        }
    }
}
</script>
