<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="Billing" SubModuleCode="" :LegendArray="LegendArray"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="SERVICEID" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
            :isShowHeaderText="false" :HeaderText="$t('BillingCondition')" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'SERVICENAME'">
                    <td>
                        <a @click="EditRecord(data.row)" class="text-primary">
                            {{ data.row.SERVICENAME }}
                        </a>
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'MODIFIED_AT'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.MODIFIED_AT">
                        {{ data.row.MODIFIED_AT != null ? $d(data.row.MODIFIED_AT) : '--' }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'BILLING_CONFIG_NAME'">
                    <span>
                        {{ data.row.BILLING_CONFIG_NAME ? data.row.BILLING_CONFIG_NAME : '--' }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS'">

                    <div v-if="data.row.SCOUNT == 0">
                        <listing-status :isfor="'BillingService'" ref="updatestatus" v-bind:dataid="data.row.SERVICEID"
                            data-controller="BillingService" v-bind:statuslist="StatusListForListing"
                            v-bind:item="data.row.STATUSID" data-action="index"
                            v-bind:dataselected="data.row.STATUSID == 1001 ? 'Active' : 'Inactive'"
                            v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                        </listing-status>
                        <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.STATUSID, 'Ticketing')">
                            <span v-bind:class="getStatusClass(data.row.STATUS)">
                                {{ data.row[data.column.COLUMN_NAME] }}
                            </span>
                        </a>
                    </div>
                    <div v-else>
                        <!-- , 'STATUSID' -->
                        <span v-bind:class="getStatusClassList(data.row.STATUS)" v-if="data.row.STATUS == 'Active'">
                            {{ data.row.STATUS }}
                        </span>
                        <span v-bind:class="getStatusClassList(data.row.STATUS)" v-if="data.row.STATUS == 'Inactive'">
                            {{ data.row.STATUS }}
                        </span>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'SCOUNT'">
                    <span>
                        <a id="ancProjectDetail" :class="{ 'text-dark text-center': true, 'disabled': (data.row.SCOUNT == 0) }"
                        href="javascript:;" v-on:click="OpenBillingRuleListing(data.row)">
                        <i class="fa fa-file-o" aria-hidden="true"></i>
                        <span class="noti-circle noti-orange">{{ data.row.SCOUNT }}</span>
                        </a>
                    </span>
                </template>
            </template>
        </tg-list>
        <div class="my-popups">
            <loader :is-visible="isLoading" />
            <div class="modal d-block" v-if="isCloneModel">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">
                                {{ $t('CloneCondition') + ': ' + this.templateName }}
                            </h5>
                            <button type="button" class="close" @click="CloseCloneModel">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <dynamic-form lang="en" ref="validateClone" :buttons="buttons" :schema="FormSchema"
                                :mandatorytext="$t('MandatoryString')" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <ServiceHistory v-if="IsHistory" :ServiceId="ServiceId"></ServiceHistory>
        <ModalAddBillingRule v-if="isModalAddRule" :serviceDetailForBillingRule="serviceDetailForBillingRule"
            :reqFrom="reqFrom" :configurationId="configurationId" :conditionId="conditionId" :nodeName="nodeName">
        </ModalAddBillingRule>

         <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isOpenRuleList">
                    <div class="modal-dialog modal-lg">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h4 class="modal-title">{{ $t('MappedBillingRule') }}</h4>

                                <button type="button" class="close" v-on:click="CloseList">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>

                            </div>

                            <div class="modal-body">
                                  <div class="listing" v-if="RuleListingData && RuleListingData.length">
                                    <div class="table-responsive table-fix-header mt-2 px-0">
                                        <table id="example" class="table table-bordered mb-0" style="width:100%;">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th style="min-width:150px;"><span class="font-weight-500">{{ $t('Billing Rule Name') }}</span></th>
                                                    <th style="min-width:150px;"><span class="font-weight-500">{{ $t('ServiceName') }}</span></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item, index) in RuleListingData" :key="index">
                                                    <td>{{ item.BILLING_CONFIG_NAME }}</td>
                                                    <td>{{ item.service_name }}</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>

                            <div class="modal-footer">
                                <button @click="CloseList" class="btn btn-danger">
                                    <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('Cancel') }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import Schema from "./BillingServiceSchema";
import DataServices from "@/services/DataService";
import ServiceHistory from './ServiceHistory.vue';
import ModalAddBillingRule from "../../components/Modals/modalAddBillingRule.vue";
export default {
    components: { ServiceHistory, ModalAddBillingRule },
    data() {
        return {
            reqFrom: 'fromBillingService',
            isCloneModel: false,
            templateName: '',
            isModalAddRule: false,
            isLoading: false,
            conditionId: 0,
            LegendArray: [],
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            Headers: [{
                "COLUMN_NAME": "SERVICENAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ConditionName'),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "SCOUNT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('BillingRuleName'),
                "DISPLAY_ORDER": 2,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "CATEGORY",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('CategoryName'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "SETUP",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('SetupName'),
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "MODIFIED_AT",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('LastUpdatedDate'),
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "UPDATEDBY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('LastUpdatedBy'),
                "DISPLAY_ORDER": 6,
            },
            {
                "COLUMN_NAME": "STATUS",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Status'),
                "DISPLAY_ORDER": 7,
            }
            ],
            leftSearchFields: [{
                fieldName: this.$t('Search By Condition Name'),
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            FormSchema: [{
                layoutType: 'single',
                Data: [
                    {
                        astype: 'TextField',
                        label: this.$t('ConditionName'),
                        name: 'ServiceName',
                        value: "",
                        placeholder: '',
                        validationRules: {
                            required: true
                        },
                        disabled: false,
                    }
                ]
            }],
            buttons: [
                {
                    type: 'submit',
                    class: 'btn btn-success',
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.CreateClone
                },
                {
                    type: 'button',
                    class: 'btn btn-danger',
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseCloneModel
                }
            ],
            CloneItem: [],
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ListingData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            UserType: '',
            UserId: '',
            setupId: '',
            servicename: '',
            IsHistory: false,
            ServiceId: '',
            isOpenRuleList: false,
            RuleListingData: [],
        }
    },
     updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("BillingCondition");

            divElement[0].appendChild(spanElement);
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
        vm.UserId = this.GetUserInfo.ID;
        vm.UserType = vm.GetUserInfo.UserType;

    },
    methods: {
        EditRecord(item) {
            var vm = this;
            vm.conditionId = item.SERVICEID;
            vm.nodeName = item.IS_LEAF_NODE;
            vm.configurationId = item.CONFIGURATION_ID;
            vm.reqFrom = 'fromBillingService';
            vm.isModalAddRule = true;
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchCondition) {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddService":
                    this.AddBillingRule();
                    break;
                case "Delete":
                    this.DeleteData();
                    break;
            }
        },
        AddBillingRule: function (item) {
            var vm = this;
            vm.serviceDetailForBillingRule = item;
            vm.conditionId = 0;
            vm.reqFrom = 'addBillingService';
            vm.isModalAddRule = true;

        },
        DeleteData: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Condition")), function () {
                    let url = `ids=${val}&tableName=TALYGEN_BE_SERVICE_MASTER&primaryKey=SERVICE_ID`;
                    DataServices.Delete(url).then(response => {
                        if (response.data) {
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            if (this.SortBy == "MODIFIED_AT") {
                this.SortBy = "LASTUPDATEDDATE"
            }
            if (this.SortBy == "UPDATEDBY") {
                this.SortBy = "LASTUPDATEDBY"
            }
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        getStatusClass: function (status) {
            var returnClass = "";
            switch (status) {
                case "Active":
                    returnClass = "px-2 text-center bg-success text-white d-block";
                    break;
                case "Inactive":
                    returnClass = "px-2 text-center bg-danger text-white d-block";
                    break;
                default:
                    returnClass = "";
            }
            return returnClass;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Reason':
                        action.isVisible = true;
                        break;
                    case 'Resource Impact':
                        action.isVisible = true;
                        break;
                    case 'Status History':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        OpenHistoryModal: function (item) {
            var vm = this;
            vm.ServiceId = item.SERVICEID;
            vm.IsHistory = true;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case "ViewHistory":
                    this.OpenHistoryModal(item);
                    break;
                case "ResourceImpact":
                    this.ResourceImpact(item);
                    break;
                case "CloneService":
                    this.CloneService(item);
                    break;
            }
        },
         OpenBillingRuleListing: async function(item) {
            var vm = this;
            vm.descriptionData  = item.CLIENT_MAPPING_LIST;
             try {
            if (typeof vm.descriptionData === 'string') {
                vm.RuleListingData = JSON.parse(vm.descriptionData);
            }
            } catch (e) {
                console.error("Invalid descriptionData JSON", e, vm.descriptionData);
                vm.RuleListingData = [];
            }
            this.isOpenRuleList = !this.isOpenRuleList;
        },
        CloseList: function () {
            var vueObj = this;
            vueObj.isOpenRuleList = false;
        },
        CloneService(item) {
            var vm = this;
            vm.templateName = item.SERVICENAME;
            vm.isCloneModel = true;
            vm.CloneItem = item;
        },
        CloseCloneModel() {
            this.isCloneModel = false;
        },
        FetchData: async function () {
            var vm = this;
            const url = `serviceName=${vm.searchCondition}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&setupId=${vm.setupId}`;
            try {
                const response = await DataServices.GetBillServiceListing(url);
                if (response.data && response.data.length > 0) {
                    vm.ListingData = [];
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (["MODIFIED_AT", "STATUS", "MODIFIED_AT", "SERVICENAME","SCOUNT"].includes(column.COLUMN_NAME)) {
                            column.settings = { isInSlot: true };
                        }
                    });
                    vm.ListingData = response.data;
                    vm.ListingData.forEach((row) => {
                        ['MODIFIED_AT'].forEach((key) => {
                            if (row[key]) {
                                row[key] = new Date(row[key]);
                            }
                        });
                        row.isCheckBoxDisabled = row.SCOUNT !== 0;
                        if (row.IS_GLOBAL === true) {
                            row.additionalClass = "sample-bar";
                        }
                    });
                    vm.listheaderbuttons[1].isdisabled = true;
                    vm.TotalRecords = response.data[0].TOTALRECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;

                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                    vm.noRecords = true;
                }

            } catch (error) {
                console.error("Error fetching data:", error);
            }
        },
        CreateClone: function () {
            var vm = this;
            var cloneName = vm.$refs.validateClone.getPropertyValue("ServiceName", "value");
            var serviceId = vm.CloneItem.SERVICEID;
            var url = `serviceId=${serviceId}&serviceName=${cloneName}`;
            DataServices.CloneBillingService(url).then((response) => {
                if (response.data.status == "Success") {
                    this.ShowAlert(this.$t('AddedSuccess', [this.$t('Clone')]), "Success", true, this.$t('Alert'));
                    vm.$refs.validateClone.updateProperty('ServiceName', 'value', '');
                    vm.CloseCloneModel();
                    vm.FetchData();
                }
                else {
                    this.ShowAlert(this.$t('ErrorWhileSaving', [this.$t('BillingService')]), "danger", true, this.$t('Alert'));
                }
            });
        }

    }
}
</script>